<template>
  <div id="app">
    <nav class="navbar spotifly-bg">
      <p class="navbar-brand font-weight-bold mb-0 text-light">Spotifly</p>
      <a class="contact" href="https://www.linkedin.com/in/gabriel-valentin-794b051a9/" target="_blank">Contact</a>
    </nav>
    <main>
      <div class="container">
        <div class="row justify-content-center align-items-center" style="height: 87vh">
          <div class="col-lg-8 text-center">
            <img height="250px" class="rounded" :src="current.image">
            <p class="title">{{ current.artist }} - <span class="artist">{{ current.title }}</span></p>
            <div>
              <button class="btn prev" @click="prev"><i class="fas fa-backward text-dark"></i></button>
              <button class="btn play" v-if="!isPlaying" @click="play">Play</button>
              <button class="btn pause" v-else @click="pause">Pause</button>
              <button class="btn next" @click="next"><i class="fas fa-forward text-dark"></i></button>
            </div>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      current: {},
      index: 0,
      isPlaying: false,
      songs: [
        {
          title: 'Yummy',
          artist: 'Justin Bieber',
          image: require('./assets/images/justin.jpg'),
          src: require('./assets/media/justin-yummy.mp3')
        },
        {
          title: 'Perfect',
          artist: 'Ed Sheeran',
          image: require('./assets/images/edsheeran.jpg'),
          src: require('./assets/media/edsheeran-perfect.mp3')
        },
        {
          title: 'My Self',
          artist: 'Bazzi',
          image: require('./assets/images/bazzi.jpg'),
          src: require('./assets/media/bazzy-myself.mp3')
        },
        {
          title: 'Sange in Palme',
          artist: 'Killa Fonic',
          image: require('./assets/images/killa.jpg'),
          src: require('./assets/media/killafonic-sangeinpalme.mp3')
        },
        {
          title: 'LA PIEPT',
          artist: 'NANE',
          image: require('./assets/images/nane.jpg'),
          src: require('./assets/media/nane-lapiept.mp3'),
        },
        {
          title: 'Cu tine',
          artist: 'Dhalibeg',
          image: require('./assets/images/dhali.jpg'),
          src: require('./assets/media/dhali-cutine.mp3')
        },
      ],
      player: new Audio()
    }
  },
  methods: {
    play (song) {
      if (typeof song.src != "undefined") {
        this.current = song;

        this.player.src = this.current.src;
      }

      this.player.play();
      this.player.addEventListener('ended', function () {
        this.index++;
        if (this.index > this.songs.length - 1) {
          this.index = 0;
        }

        this.current = this.songs[this.index];
        this.play(this.current);
      }.bind(this));
      this.isPlaying = true;
    },
    pause () {
      this.player.pause();
      this.isPlaying = false;
    },
    next () {
      this.index++;
      if (this.index > this.songs.length - 1) {
        this.index = 0;
      }

      this.current = this.songs[this.index];
      this.play(this.current);
    },
    prev () {
      this.index--;
      if (this.index < 0) {
        this.index = this.songs.length - 1;
      }

      this.current = this.songs[this.index];
      this.play(this.current);
    }
  },
  created () {
    this.current = this.songs[this.index];
    this.player.src = this.current.src;
  }
}
</script>

<style>
@import url('assets/css/responsive.css');
@import url('assets/css/style.css');
</style>
